







































































import {Component, Mixins} from "vue-property-decorator";
import {envData, traversingTree} from "@/util";
import MenuList from "./MenuList.vue";
import Breadcrumb from "./Breadcrumb.vue";
import ThirdMenuList from "./ThirdMenuList.vue";
import changePass from "./changePass.vue";
import _ from 'lodash'
//登录用户信息
import LoginInfo from "@/mixins/loginInfo";
//枚举信息
import Enums from '@/mixins/enums'
import {goto, isInnerIPFn} from 'sso2-access-util'


@Component({
    name: "Layout",
    components: {
        MenuList,
        Breadcrumb,
        ThirdMenuList,
        changePass
    },
    mixins: [LoginInfo, Enums]
})
export default class Layout extends Mixins(LoginInfo, Enums) {
    public $refs!: {
        menuList: MenuList,
        changePass: any
    }

    sysName = envData.VUE_APP_SYS_NAME;

    isInit = false
    isError = false

    // get getSystemName() {
    //     return this.$store.state.SYSTEM_INFO.name || ''
    // }

    //当前激活的菜单
    get activeMenu() {
        let path = '/main';
        _.forEachRight(this.$route.matched, (item) => {
            if (item.meta && item.meta.isMenu) {
                path = item.path;
                return false
            }
        })
        return path;
    }

    //左侧所有的菜单
    get leftMenuPaths() {
        let paths: string[] = [];
        //授权菜单显示
        traversingTree(this.$refs.menuList.menuListAuc, {
            childList: 'children',
            filterFun: (item: any) => {
                //非菜单时不再加载子级
                if (!item.meta) {
                    return true
                }
                //可以展开的菜单或者未授权的菜单跳过
                if (item.meta && (item.meta.isExpand || !item.meta.isShow || item.meta.title)) {
                    return true
                }

                //只取叶子节点的路由,目前不存在有展开并且可以点开的菜单页面
                if (paths.length) {
                    let flag = true;
                    for (let [index, menuPath] of paths.entries()) {
                        if (_.startsWith(item.path, menuPath)) {
                            paths[index] = item.path
                            flag = false;
                        }
                    }
                    if (flag) {
                        paths.push(item.path);
                    }
                } else {
                    paths.push(item.path);
                }
            }
        });
        return paths
    }

    //当前地址是否授权
    get isAccredit() {
        //首页跳过验证
        if (['/main'].includes(this.activeMenu)) {
            return true
        }
        let res = false;
        //左侧菜单激活在授权地址内
        _.forEach(this.leftMenuPaths, (item) => {
            if (_.startsWith(item, this.activeMenu)) {
                res = true;
                return false;
            }
        });
        return res;
    }

    //用户应用列表
    get userAppList() {
        if (this.LOGIN_INFO && this.LOGIN_INFO.userAppList) {
            return this.LOGIN_INFO.userAppList
        } else {
            return []
        }
    }

    //获取某个系统的跳转地址
    getSysUrl(menuCode: string) {
        let resUrl = '';
        for (let app of this.userAppList) {
            if (app.code === menuCode) {
                resUrl = app.domainPub;
                if (isInnerIPFn() || !resUrl) {
                    resUrl = app.domain
                }
                break;
            }
        }
        return resUrl;
    }

    async toSelectSys() {
        await goto.toUrlWithTicketBySync(this.getSysUrl('sso'));
    }

    messageFun() {
        this.$message({
            message: '研发中',
            type: 'warning'
        });
    }

    onSysMenu(c: string) {
        switch (c) {
            case 'pass':
                this.changePassFun();
                break
            case 'logout':
                this.out();
                break
        }
    }

    out() {
        this.$confirm('此操作将退出系统，返回登录页，是否继续？', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            this.logout();
            // this.$router.replace("/login");
        })
    }

    changePassFun() {
        this.$refs.changePass.open();
    }

    async mounted() {
        try {
            await Promise.all([this.syncLoginInfo(), this.syncEnums()])
            this.isInit = true;
            if (this.$route.path === '/main') {
                await this.$router.replace(this.leftMenuPaths[0]);
            }
        } catch (e) {
            this.isError = true;
        }
    }

}
