



















/**
 * 面包屑加搜索
 */
import {Component, Vue} from 'vue-property-decorator';
import _ from 'lodash'

@Component
export default class Breadcrumb extends Vue {

    get breadcrumbList() {
        let names = this.$route.matched.map(item => item.meta.title || item.name)
        return _.tail(names)
    }
}
