































import {Vue, Component} from 'vue-property-decorator';
import Open, {OpenInterface} from "@/components/Open.vue";
import {Form} from "element-ui";
import {sso_web} from "@/api";
import {md5} from 'md5js';
import _ from 'lodash';

@Component({
    components: {Open}
})
export default class pass extends Vue {

    $refs!: {
        open: Open,
        form: Form
    }

    ruleForm: any = {}

    rules: any = {
        oldPassword: [
            {required: true, message: '请输入密码', trigger: 'blur'}
        ],
        newPassword: [
            {required: true, validator: this.validatePass, trigger: 'blur'}
        ],
        checkPass: [
            {required: true, validator: this.validatePass2, trigger: 'blur'}
        ],
    }

    validatePass(rule: any, value: string, callback: any) {
        let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,12}$/;
        if (!value) {
            callback(new Error('请输入密码'));
        } else if (!reg.test(value)) {
            callback(new Error('请输入8-12位英文和数字组合'));
        } else {
            if (this.ruleForm.checkPass !== '') {
                this.$refs.form.validateField('checkPass');
            }
            callback();
        }
    }

    validatePass2(rule: any, value: string, callback: any) {
        if (!value) {
            callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.newPassword) {
            callback(new Error('两次输入密码不一致!'));
        } else {
            callback();
        }
    }

    submit() {
        this.$refs.form.validate((valid: boolean) => {
            if (valid) {
                let params = _.cloneDeep(this.ruleForm);
                delete params.checkPass;
                params.newPassword = md5(params.newPassword, 32);
                params.oldPassword = md5(params.oldPassword, 32);
                sso_web.sso_web_sso_modifyPassword_post({
                    data: {
                        ...params
                    }
                }).then((res: any) => {
                    this.$notify({
                        title: '成功',
                        message: res.message,
                        type: 'success'
                    });
                    this.close();
                })
            }
        })
    }

    close() {
        this.$refs.open.close();
        this.$emit('on-close', '')
    }

    open(uid: string) {
        this.$refs.open.open();
        this.ruleForm = {
            userUid: uid
        };
    }
}
